import { Box, SimpleGrid, Text } from '@chakra-ui/react'
import React from 'react'
import styles from "../css/softskills.module.css"

export const SoftSkills = () => {
  return (

<div >

<Box className={styles.softContainer}>

<Text fontSize={['lg','3xl',  '5xl']} mt="2" mb="5"  color='white' textAlign="center"
className={styles.softTitle}>Soft Skills</Text>

<SimpleGrid minChildWidth='120px' height="auto" spacing='40px' margin="10" m="auto" >
  <Box  height='120px' className={styles.softSkills}>
    <Text fontSize={['xs','md', 'lg']}>Problem Solving</Text>
  </Box>
  <Box  height='120px' className={styles.softSkills}>
  <Text fontSize={['xs','md', 'lg']}>Communication
</Text>
  </Box>
  <Box  height='120px' className={styles.softSkills}>
  <Text fontSize={['xs','md', 'lg']}>Work Ethics</Text>
  </Box>
  <Box  height='120px' className={styles.softSkills}>
  <Text fontSize={['xs','md', 'lg']}>Adaptability</Text>
  </Box>
</SimpleGrid>

</Box>

</ div>

  )
}
