import { Box, SimpleGrid, Text, CircularProgress, CircularProgressLabel  } from '@chakra-ui/react'
import React from 'react'
import styles from "../css/stats.module.css"

export const Stats = () => {
  return (
    <div>



<Text fontSize={['lg','3xl','5xl']} mt="2" textAlign="center"  className={styles.statsTitle} color='white' >Statistics</Text>

<SimpleGrid minChildWidth='120px'  height="auto" spacing='30px' margin="10" m="auto" className={styles.container}
  w="70%">

  <Box>
  <CircularProgress value={100} color='#7040c2' size={['8rem','8rem', '12rem',  '15rem']}>
  <CircularProgressLabel fontSize={['xs','md', '3xl']}>1000</CircularProgressLabel>
  <Text fontSize={['xs','md',  'xl']} mt="2">Hours of Soft Skills</Text>
  </CircularProgress>
  </Box>

  <Box>
  <CircularProgress value={100} color='#7040c2' size={['8rem','8rem', '12rem',  '15rem']}>
  <CircularProgressLabel fontSize={['xs','md', '3xl']}>76+</CircularProgressLabel>
  <Text fontSize={['xs','md',  'xl']}>Git Commits</Text>
  </CircularProgress>
  </Box>

  <Box>
  <CircularProgress value={100} color='#7040c2' size={['8rem','8rem', '12rem',  '15rem']}>
  <CircularProgressLabel fontSize={['xs','md', '3xl']}>10+</CircularProgressLabel>
  <Text fontSize={['xs','md',  'xl']}>Project Done</Text>
  </CircularProgress>
  </Box>

  <Box>
  <CircularProgress value={100} color='#7040c2' size={['8rem','8rem', '12rem',  '15rem']}>
  <CircularProgressLabel fontSize={['xs','md', '3xl']}>240+</CircularProgressLabel>
  <Text fontSize={['xs','md',  'xl']}>DSA Problems Solved</Text>
  </CircularProgress>
  </Box>

</SimpleGrid>

</div>

  )
}
